export default {
	data() {
		return {
			order:null,
			first:true //是否是第一次支付
		};
	},
	onLoad(params){
	    const paramsObj = JSON.parse(params.paramsStr)
	    this.initData(paramsObj)
	},
	computed:{
		total(){
			if(this.order){
				return Number(this.order.totalPrice) + this.order.seats.length * 3
			}
			
		},
		price(){
			return Number(this.order.price) + 3
		}
	},
	methods: {
		initData(params){
		    this.order = params
		  },
		  //模拟支付
		  payment(){
		    //避免重复支付
		    if(this.first){
		      let movieOrder = uni.getStorageSync('movieOrder') || []
		      movieOrder.unshift(this.order)
		      uni.setStorageSync('movieOrder', movieOrder)
		      uni.showToast({
		        title: '支付成功',
		      })
		      this.first = false
		    } else {
		      uni.showToast({
		        title: '已支付',
		        icon:'none'
		      })
		    }
		  }
	},
}
